<template>
  <div>
    <el-table :data="goodsList" style="width: 100%">
      <el-table-column prop="id" label="id" min-width="80"></el-table-column>
      <el-table-column
        prop="firstcatename"
        label=""
        min-width="180"
      ></el-table-column>
      <el-table-column
        prop="secondcatename"
        label=""
        min-width="180"
      ></el-table-column>
      <el-table-column
        prop="goodsname"
        label=""
        min-width="180"
      ></el-table-column>
      <el-table-column prop="img" label="" min-width="180">
        <template v-slot="props">
          <img :src="$imgBaseUrl + props.row.img" style="height: 80px" alt />
        </template>
      </el-table-column>
      <el-table-column prop="price" label="" min-width="180">
        <template v-slot="props">&yen;{{ props.row.price }}</template>
      </el-table-column>
      <el-table-column prop="market_price" label="" min-width="180">
        <template v-slot="props">&yen;{{ props.row.market_price }}</template>
      </el-table-column>
      <el-table-column prop="isnew" label="" min-width="180">
        <template v-slot="props">
          <el-tooltip
            :content="props.row.isnew === 0 ? '' : ''"
            placement="top"
          >
            <el-switch
              @change="statusChange(props.row, 'isnew')"
              v-model="props.row.isnew"
              :active-value="1"
              :inactive-value="0"
            ></el-switch>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column prop="ishot" label="" min-width="180">
        <template v-slot="props">
          <el-tooltip
            :content="props.row.ishot === 0 ? '' : ''"
            placement="top"
          >
            <el-switch
              @change="statusChange(props.row, 'ishot')"
              v-model="props.row.ishot"
              :active-value="1"
              :inactive-value="0"
            ></el-switch>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="" min-width="180">
        <template v-slot="props">
          <el-tag type="success" v-if="props.row.status === 1"></el-tag>
          <el-tag type="danger" v-else></el-tag>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="" min-width="120">
        <template v-slot="props">
          <el-button
            @click="edit(props.row)"
            type="success"
            circle
            icon="el-icon-edit"
            size="mini"
          ></el-button>
          <el-button
            @click="del(props.row.id)"
            type="danger"
            circle
            icon="el-icon-delete"
            size="mini"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @current-change="pageChange"
      :page-size="size"
      background
      layout="prev, pager, next"
      :total="goodsTotal"
    ></el-pagination>
  </div>
</template>
<script>
// 
import vm from "../../utils/vm";
export default {
  props: ["goodsList", "goodsTotal", "size", "page"],
  methods: {
    //   
    pageChange(page) {
      // 1-this.$emit()Index
      this.$emit("updateList", page);
    },
    // 
    // row: 
    // attr: (isnew,ishot)
    statusChange(row, attr) {
      // 
      // FormData
      const fd = new FormData();
      fd.append("id", row.id);
      fd.append(attr, row[attr]);
      this.$http.post("/goodsedit", fd).then((res) => {
        if (res.code === 200) {
          return this.$success(res.msg);
        }
        this.$error(res.msg);
      });
    },
    // 
    edit(row) {
      // 1-Modal
      vm.$emit("sendGoodsData", { ...row });
    },
    // 
    del(id) {
      this.$myConfirm(() => {
        this.$http.post("/goodsdelete", { id }).then((res) => {
          if (res.code === 200) {
            this.$success(res.msg);
            // 
            this.$emit("updateList");
            //&&
            if (
              Math.ceil(this.goodsTotal / this.size) === this.page &&
              this.goodsTotal % this.size === 1
            ) {
              // 
              this.$emit("updateList", this.page - 1);
            }
          } else {
            this.$error(res.msg);
          }
        });
      });
    },
  },
};
</script>

<style>
</style>


