<template>
  <el-dialog
    @close="reset"
    :title="formData.id > 0 ? '' : ''"
    :visible.sync="info.isShow"
    width="100%"
  >
    <el-form ref="formRef" :model="formData" :rules="rules" label-width="80px">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="first">
          <el-form-item label="" prop="first_cateid">
            <el-select
              @change="firstCateChange"
              v-model="formData.first_cateid"
            >
              <el-option
                v-for="item in cateList"
                :key="item.id"
                :label="item.catename"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="second_cateid">
            <el-select v-model="formData.second_cateid">
              <el-option
                v-for="item in secondCateList"
                :key="item.id"
                :label="item.catename"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="goodsname">
            <el-input
              placeholder=""
              v-model="formData.goodsname"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="img">
            <el-upload
              action="#"
              :auto-upload="false"
              list-type="picture-card"
              :on-preview="handlePictureCardPreview"
              :limit="1"
              :on-exceed="limitTips"
              :on-change="imgChange"
              :file-list="imgList"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
              <img width="100%" :src="dialogImageUrl" alt />
            </el-dialog>
          </el-form-item>
          <el-form-item label="" prop="price">
            <el-input
              placeholder=""
              v-model="formData.price"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="market_price">
            <el-input
              placeholder=""
              v-model="formData.market_price"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="specsid">
            <el-select @change="specsChange" v-model="formData.specsid">
              <el-option
                v-for="item in specsList"
                :key="item.id"
                :label="item.specsname"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="specsattr">
            <el-select v-model="formData.specsattr" multiple>
              <el-option
                v-for="(item, index) in specsAttrs"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" prop="isnew">
            <el-radio-group v-model="formData.isnew">
              <el-radio :label="0"></el-radio>
              <el-radio :label="1"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="ishot">
            <el-radio-group v-model="formData.ishot">
              <el-radio :label="0"></el-radio>
              <el-radio :label="1"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-radio-group v-model="formData.status">
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
            </el-radio-group>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <el-form-item prop="description" label-width="0">
            <vueWangeditor
              ref="description"
              v-model="formData.description"
              id="description"
              width="100%"
              height="300"
              style="height: 400px"
            />
          </el-form-item>
        </el-tab-pane>
      </el-tabs>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="reset"> </el-button>
      <el-button type="primary" @click="submit"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
// vue-wangeditor
import vueWangeditor from "vue-wangeditor";
// vuex
import { mapState, mapActions } from "vuex";
// 
import vm from "../../utils/vm";
export default {
  created() {
    //   
    this.getCateListAction();
    // 
    this.getSpecsListAction();
    // sendGoodsData
    vm.$on("sendGoodsData", (obj) => {
      // , 
      obj.specsattr = obj.specsattr.split(",");
      // 1-
      this.formData = obj;
      // 2-
      this.info.isShow = true;
      // 3-
      // this.$nextTick
      // this.$nextTick(()=>{});
      setTimeout(() => {
        this.$refs.description.setHtml(obj.description);
      }, 500);
      // change
      this.firstCateChange(obj.first_cateid, false);
      // change
      this.specsChange(obj.specsid, false);
      // 
      this.imgList =
        obj.img !== "" ? [{ url: this.$imgBaseUrl + obj.img }] : [];
    });
  },
  computed: {
    ...mapState({
      cateList: (state) => state.cate.cateList,
      specsList: (state) => state.specs.specsList,
    }),
  },
  components: {
    vueWangeditor,
  },
  props: ["info"],
  data() {
    // 
    const validDesc = (rule, value, callback) => {
      // 
      if (this.$refs.description.getHtml() === "<p><br></p>") {
        return callback(new Error(""));
      }
      // 
      callback();
    };
    return {
      // 
      dialogImageUrl: "",
      dialogVisible: false,
      activeName: "first",
      //   
      imgList: [],
      // 
      secondCateList: [],
      //   
      specsAttrs: [],
      // 
      formData: {
        first_cateid: "", // id
        second_cateid: "", //id
        goodsname: "", //
        price: "", // 
        market_price: "", //
        img: "", // 
        description: "", // 
        specsid: "", // id
        specsattr: "", // 
        isnew: "", // 
        ishot: "", // 
        status: "", //
      },
      //  
      rules: {
        first_cateid: { required: true, message: "", trigger: "blur" },
        second_cateid: { required: true, message: "", trigger: "blur" },
        goodsname: { required: true, message: "", trigger: "blur" },
        price: { required: true, message: "", trigger: "blur" },
        market_price: { required: true, message: "", trigger: "blur" },
        description: { validator: validDesc, trigger: "blur" },
        specsid: { required: true, message: "", trigger: "blur" },
        specsattr: { required: true, message: "", trigger: "blur" },
        isnew: { required: true, message: "", trigger: "blur" },
        ishot: { required: true, message: "", trigger: "blur" },
        status: { required: true, message: "", trigger: "blur" },
      },
    };
  },
  methods: {
    ...mapActions("cate", ["getCateListAction"]),
    ...mapActions("specs", ["getSpecsListAction"]),
    handleClick(tab, event) {
      console.log(tab, event);
    },
    // 
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    limitTips() {
      this.$error("");
    },
    // 
    // flag=true: , 
    // flag=false: 
    firstCateChange(id, flag = true) {
      // 1-id
      const index = this.cateList.findIndex((item) => item.id === id);
      if (flag) {
        //   2-secondCateList
        this.secondCateList = [];
      }
      // 3-this.secondCateList
      this.secondCateList = this.cateList[index].children;
    },
    // 
    // 
    // flag=true, 
    // flag=false, 
    specsChange(id, flag = true) {
      // 1-id
      const index = this.specsList.findIndex((item) => item.id === id);
      // 2-this.specsAttrs
      console.log(this.specsList[index]);
      this.specsAttrs = this.specsList[index].attrs;
      //   , 
      if (flag) {
        // 3-
        this.formData.specsattr = [];
      }
    },
    // 
    imgChange(file) {
      // 
      this.formData.img = file.raw;
    },
    // 
    submit() {
      //   
      this.formData.description = this.$refs.description.getHtml();
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          // , 
          const fd = new FormData();
          // 
          delete this.formData.firstcatename;
          delete this.formData.secondcatename;
          for (let key in this.formData) {
            fd.append(key, this.formData[key]);
          }
          const url = this.formData.id > 0 ? "/goodsedit" : "/goodsadd";
          this.$http.post(url, fd).then((res) => {
            if (res.code === 200) {
              this.$success(res.msg);
              //  
              this.$emit("updateList");
              //  
              this.reset();
            } else {
              this.$error(res.msg);
            }
          });
        }
      });
    },
    // 
    reset() {
      // 
      this.formData = {
        first_cateid: "", // id
        second_cateid: "", //id
        goodsname: "", //
        price: "", // 
        market_price: "", //
        img: "", // 
        description: "", // 
        specsid: "", // id
        specsattr: "", // 
        isnew: "", // 
        ishot: "", // 
        status: "", //
      };
      // 
      this.$refs.formRef.resetFields();
      // 
      this.$refs.description.setHtml("");
      // 
      this.info.isShow = false;
      // 
      this.imgList = [];
    },
  },
};
</script>

<style>
</style>


