<template>
  <el-card>
    <el-button
      @click="info.isShow = !info.isShow"
      type="primary"
      icon="el-icon-circle-plus-outline"
      ></el-button
    >
    <el-divider />
    <!-- List -->
    <List
      @updateList="updateList"
      :goodsList="goodsList"
      :goodsTotal="goodsTotal"
      :size="size"
      :page="page"
    />
    <!--  -->
    <Modal :info="info" @updateList="updateList" />
  </el-card>
</template>

<script>
// vuex
import { mapState, mapActions } from "vuex";
// List
import List from "./List";
import Modal from "./Modal";

export default {
  data() {
    return {
      info: { isShow: false },
      page: 1, // 
      size: 5, // 
    };
  },
  components: {
    List,
    Modal,
  },
  created() {
    this.getGoodsListAction({ page: this.page, size: this.size });
    this.getGoodsTotalAction();
  },
  computed: Object.assign(
    {
      // 
    },
    mapState({
      goodsList: (state) => state.goods.goodsList,
      goodsTotal: (state) => state.goods.goodsTotal,
    })
  ),
  methods: {
    ...mapActions("goods", ["getGoodsListAction", "getGoodsTotalAction"]),
    // updateList
    updateList(page) {
      // 
      if (page) {
        this.page = page;
      }
      // 
      this.getGoodsListAction({ page: this.page, size: this.size });
      this.getGoodsTotalAction();
    },
  },
};
</script>

<style>
</style>


